.qualification-examine-container {
  width: 100%;
}
.qualification-examine-container .disabled-column {
  pointer-events: none;
}
.qualification-examine-container .single-line {
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: break-all;
  white-space: nowrap;
}
.qualification-examine-container .attachment-wrapper-disabled {
  color: #212121 !important;
  pointer-events: none;
}
.qualification-examine-container .attachment-wrapper-disabled .kdicon {
  color: #212121 !important;
}
.qualification-examine-container .qualification-examine-warpper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.qualification-examine-container .qualification-examine-warpper > div {
  margin-bottom: 20px;
}
.qualification-examine-container .qualification-examine-warpper .examine-header {
  font-family: PingFangSC-Semibold;
  font-size: 16px;
  color: #212121;
  letter-spacing: 0;
  line-height: 24px;
  font-weight: 600;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
}
.qualification-examine-container .qualification-examine-warpper .examine-header::before {
  content: "";
  display: inline-block;
  background: var(--theme-color);
  border-radius: 3px;
  width: 6px;
  height: 14px;
  margin-right: 6px;
}
.qualification-examine-container .attachment-wrapper {
  display: flex;
  cursor: pointer;
  color: #0e5fd8;
}
.qualification-examine-container .attachment-wrapper .kdicon {
  margin-right: 5px;
  color: #0e5fd8;
  font-size: 16px;
}
.qualification-examine-container .kd-table-wrapper {
  padding: 0 12px;
}
.qualification-examine-container .kd-table {
  border-bottom: 1px solid #d9d9d9;
}
.qualification-examine-container .kd-table > .kd-table-body td {
  border: none;
  font-size: 12px;
  letter-spacing: 0;
  line-height: 18px;
  font-weight: 400;
  padding: 4px 6px;
}
.qualification-examine-container .kd-table .kd-table-header {
  border-bottom: none;
}
.qualification-examine-container .kd-table .kd-table-header thead th {
  background: #f3f5f8;
  border: none;
  color: #212121;
  letter-spacing: 0;
  line-height: 18px;
  font-weight: 400;
  padding: 4px 6px;
}
.qualification-examine-container .expend-table th[data-code="selfAssess"],
.qualification-examine-container .expend-table th[data-code="selfAssRes"],
.qualification-examine-container .expend-table th[data-code="selfAssSum"],
.qualification-examine-container .expend-table th[data-code="fileNum"],
.qualification-examine-container .expend-table th[data-code="selfAssResType"],
.qualification-examine-container .expend-table td[data-code="selfAssRes"],
.qualification-examine-container .expend-table td[data-code="selfAssSum"],
.qualification-examine-container .expend-table td[data-code="selfAssResType"],
.qualification-examine-container .expend-table td[data-code="fileNum"] {
  border-top: 1px solid #d9d9d9 !important;
  border-left: 1px solid #d9d9d9 !important;
}
.qualification-examine-container .expend-table th[data-code="selfAssess"],
.qualification-examine-container .expend-table th[data-code="fileNum"],
.qualification-examine-container .expend-table td[data-code="fileNum"] {
  border-right: 1px solid #d9d9d9 !important;
}
.qualification-examine-container .expend-table th[data-code="selfAssess"] {
  border-top: none !important;
}
.qualification-examine-container .special-tabel th,
.qualification-examine-container .special-tabel td {
  border: none !important;
}
.qualification-examine-container .unexpend-table thead .kd-table-header-row:nth-child(2) {
  display: none;
}
.qualification-examine-container-view {
  pointer-events: nones;
}
